<template>
  <div class="main clearfix">
    <!-- circlePlayer 播放器 -->
    <div id="jquery_jplayer" class="cp-jplayer"></div>
    <div class="wrapper">
    <!-- 封面 -->
      <div class="circle">
        <img src="../../static/images/cover.jpg" alt="singger avatar" class="cover">
      </div>
      <div id="cp_container" class="cp-container">
        <div class="cp-buffer-holder">
          <div class="cp-buffer-1"></div>
          <div class="cp-buffer-2"></div>
        </div>
        <div class="cp-progress-holder">
          <div class="cp-progress-1"></div>
          <div class="cp-progress-2"></div>
        </div>
        <!-- 控制进度hook -->
        <div class="cp-circle-control"></div>
        <div class="cp-circle-inner"></div>
        <!-- 控制按钮组 -->
        <div class="cp-controller">
          <a href="javascript:void(0);" class="control-item-prev" v-on:click="prev"><img src="../../static/images/player/prev.png" alt="player-control"></a>
          <a href="javascript:void(0);" class="control-item-pause" v-on:click="play"><img src="../../static/images/player/play.png" alt="player-control"></a>
          <a href="javascript:void(0);" class="control-item-play" v-on:click="pause"><img src="../../static/images/player/pause.png" alt="player-control"></a>
          <a href="javascript:void(0);" class="control-item-next" v-on:click="next"><img src="../../static/images/player/next.png" alt="player-control"></a>
        </div>
      </div>
    </div>
    <ul class="music-list">
      <li v-for="(music,index) in musics" class="music-item clearfix" v-bind:class="{active:music.active}" v-on:click="onPlay(music)">
        <div class="banner-left">
          <a href="javascript:void(0);" v-if="music.active"><img src="../../static/images/player/pause.png"></a>
          <a href="javascript:void(0);" v-if="!music.active"><img src="../../static/images/player/play.png"></a>
        </div>
        <a href="javascript:void(0);" class="music-name ellipsis">{{music.name}}</a>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Music',
  data () {
    return {
      musics:require('../music.js')
    };
  },
  methods: {
    onPlay(music){
      const index = this.musics.indexOf(music);
      this.delActive();

      music.active = true;
      this.$set(this.musics,index,music);
      this.circlePlayer.setMedia({
        mp3: music.src
      });
      this.play();
    },

    delActive(){
      this.musics.some(music => {
        if(music.active){
          music.active = false;
          return true;
        }
      });
    },

    play(evt){
      $('.control-item-pause').hide();
      $('.control-item-play').show();
      this.circlePlayer.play();
    },

    pause(evt){
      $('.control-item-play').toggle();
      $('.control-item-pause').toggle();
      this.circlePlayer.pause();
    },

    next() {
      let musics = this.musics,
        currentInd = musics.indexOf(musics.find(m => m.active)),
        length = this.musics.length;

      if (currentInd === length - 1) {
        this.onPlay(musics[0]);
      } else {
        this.onPlay(musics[currentInd + 1]);
      }
    },

    prev() {
      let musics = this.musics,
        currentInd = musics.indexOf(musics.find(m => m.active)),
        length = this.musics.length;

      if (currentInd === 0) {
        this.onPlay(musics[length - 1]);
      } else {
        this.onPlay(musics[currentInd - 1]);
      }
    },

  },
  mounted() {
    let circlePlayer = new CirclePlayer('#jquery_jplayer',{
        mp3: this.musics[0].src
      },{
        cssSelectorAncestor: "#cp_container",
        swfPath: './static',
        wmode: 'window',
        supplied: 'mp3,m4a,oga',
        loop: false
      });
    this.circlePlayer = circlePlayer;
    circlePlayer.player.on($.jPlayer.event.ended + circlePlayer.eventNamespace, (evt) => {
      this.next();
    });
    circlePlayer.player.on($.jPlayer.event.ready + circlePlayer.eventNamespace, (evt) => {
      this.play();
    });
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
html {
  margin:0;
  padding: 0;
  font-family: PingFangSC-Regular, Helvetica, "Helvetica Neue", "Segoe UI", "Hiragino Sans GB", "Source Han Sans CN", "Microsoft YaHei", STHeiti, "WenQuanYi Micro Hei", sans-serif !important
}

body {
  margin:0;
  padding: 0;
  background-color: #FFF;
  color: #444;
  font-family: TIBch, "Classic Grotesque W01", "Helvetica Neue", Arial, "Hiragino Sans GB", STHeiti, "Microsoft YaHei", "WenQuanYi Micro Hei", SimSun, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px
}
a {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
.wrapper {
  position: fixed;
  bottom: 220px;
  width: 220px;
  height: 220px;
  left: 0;
  right: 0;
  top:0;
  margin: auto;
  -webkit-transition: left 0.3s ease-in-out;
  -moz-transition: left 0.3s ease-in-out;
  transition: left 0.3s ease-in-out;
}
.wrapper * {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.circle {
  position: absolute;
  top: 0px;
  border: 10px solid rgba(42, 68, 90, 0.8);
  border-radius: 50%;
}
.wrapper img{vertical-align: middle;}
.cover {
  border-radius: 50%;
}
.cp-progress-holder {
  position: absolute;
  clip: rect(0, 220px, 220px, 110px);
}
.cp-progress-1,
.cp-progress-2 {
  position: absolute;
  width: 220px;
  height: 220px;
  border: 10px solid #f45e47;
  border-radius: 50%;
  clip: rect(0, 110px, 220px, 0);
}
.cp-circle-control {
  position: absolute;
  top: 0px;
  width: 220px;
  height: 220px;
  cursor: pointer;
}
.cp-circle-inner {
  position: absolute;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  left: 10px;
  top: 10px;
}
.cp-gt50 {
  clip: auto;
}
.cp-progress-1.cp-fallback {
  clip: rect(auto, auto, auto, auto);
}
.cp-controller {
  position: absolute;
  top: 134px;
  width: 60%;
  text-align: center;
  left: 0;
  right: 0;
  margin: auto;
}
.control-item-play {
  display: none;
}
.music-list {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 20px;
  max-height: 200px;
  overflow-y: auto;
  list-style: none;
  text-align: left;
}
.music-item {
  padding: 4px;
  line-height: 40px;
}

.music-name {
  display: block;
  padding-left: 16px;
}
.banner-left {
  float: left;
  width:40px;
  height: 40px;
}
.banner-left img{
  height: 100%;
}
.clearfix::before,.clearfix::after {
  display: table;
  clear: both;
  content: ""
}
.ellipsis {
  overflow: hidden;
  word-wrap: normal;
  white-space: nowrap;
  text-overflow: ellipsis;
}
</style>



